.support-container{
    box-sizing: border-box;
    width: 335x;
    min-height: 352px;
    padding: 24px 40px;
    border-radius: 16px;
    background: radial-gradient(
    183.6% 183.01% at 59.76% -23.42%,
   #4f2ee8 18.03%,
    #ffffff 100%
  );
  box-shadow: 0px 10px 10px rgba(245, 245, 247, 0.1);
}

@media screen and (min-width: 768px){
    .support-container{
        min-height: 472px;
    }
    
}

@media screen and (min-width: 1440px){
    .support-container{
        width: 356px;
    min-height: 490px;
    }
    
}
    

