.support-title {
  color: #ffffff;
  font-style: normal;
  font-weight: 700;
  font-size: 28px;
  line-height: 32px;
  letter-spacing: -0.04em;
  display: flex;
  gap: 12px;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
}

.swiper-next {
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  border-radius: 50%;
  width: 38px;
  height: 38px;
  margin: 0 auto;
}

.swiper {
  height: 188px;
  margin: 40px 0;
}

@media screen and (min-width: 780px) {
  .swiper {
    height: 292px;
  }
}

.swiper-slide {
  display: flex !important;
  height: auto !important;
  /* display: flex ;
  height: auto ; */
}

.icon-container {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 22px;
  height: 22px;
}

.support-index {
  font-family: 'DM Sans';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
}

.support-item {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 0px;
  gap: 16px;
  color: #ffffff;
  
}

.support-image{filter: brightness(0) invert(1);
max-width: 100%;
height: auto;
}

.support-link:hover .support-image{filter: none;}

.support-list {
  display: flex;
  flex-direction: column;
  /* gap: 20px; */
 }

.support-link {
  height: 32px;
  display: flex;
  align-items: center;
}

