:root {
  --bg-color-light: #f6f6f6;
  --header-bg-color-light: #fff;
  --text-logo-bookshelf-light: #000;
  --accent-color-light-theme: #4f2ee8;
  --primary-text-color-light-theme: #111;
  --secondary-color-light-theme: #b4afaf;
  --category-color-light: rgba(17, 17, 17, .6);
  --scroll-light-theme: #e0e0e0;
  --bg-color-dark: #202024;
  --header-bg-color-dark: #111;
  --text-logo-bookshelf-dark: #f3f3f3;
  --accent-color-dark-theme: #eac645;
  --primary-text-color-dark-theme: #fff;
  --secondary-color-dark-theme: #b4afaf;
  --category-color-dark: rgba(255, 255, 255, .6);
  --scroll-datk-theme: rgba(17, 17, 17, .6);
}

.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 375px) {
  .container {
    width: 375px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    width: 1440px;
    padding-left: 24px;
    padding-right: 24px;
  }
}

.flex {
  display: flex;
}

body {
  margin: 0;
  padding: 0;
  font-family: DM Sans;
  font-style: normal;
}

a {
  text-decoration: none;
}

ul, .list {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  display: block;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

.link {
  color: currentColor;
  text-decoration: none;
}

* {
  scroll-behavior: smooth;
}

.main-container {
  width: 335px;
  grid-template-columns: 335px;
  row-gap: 40px;
  margin-top: 40px;
  margin-left: 0;
  margin-right: 0;
  padding-left: 20px;
  padding-right: 20px;
  display: grid;
}

.header {
  box-sizing: border-box;
  width: 375px;
  height: 64px;
  background: #fff;
  border: 1.5px solid #111;
  border-radius: 0 0 8px 8px;
}

.container-aside {
  width: 335px;
}

@media screen and (min-width: 768px) {
  .main-container {
    width: 704px;
    grid-template-columns: 704px;
    row-gap: 100px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 768px) {
  .header {
    width: 768px;
    height: 72px;
  }
}

@media screen and (min-width: 768px) {
  .container-aside {
    width: 704px;
  }
}

@media screen and (min-width: 1200px) and (max-width: 1440px) {
  .main-container {
    width: 1440px;
    grid-template-columns: 356px 996px;
    gap: 40px;
    padding-left: 0;
    padding-right: 0;
  }
}

@media screen and (min-width: 1200px) and (max-width: 1440px) {
  .header {
    width: 1440px;
    height: 72px;
  }
}

@media screen and (min-width: 1200px) and (max-width: 1440px) {
  .container-aside {
    width: 356px;
    grid-column-start: 1;
  }
}

.btn-main {
  min-width: 124px;
  height: 46px;
  border: 2px solid var(--accent-color-light-theme);
  letter-spacing: -.01em;
  text-transform: uppercase;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 40px;
  padding: 14px 28px;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.29;
}

.btn-main:hover, .btn-main:focus {
  background-color: var(--accent-color-light-theme);
  color: var(--header-bg-color-light);
}

.btn-shop-list {
  width: 499px;
  height: 64px;
  border: 2px solid var(--accent-color-light-theme);
  letter-spacing: -.01em;
  text-transform: uppercase;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 40px;
  font-size: 18px;
  font-weight: 700;
  line-height: 1.33;
}

