    width: 279px;
    height: 46px;
    font-size: 14px;
    line-height: 1.29;
  }
}

.btn-shop-list:hover, .btn-shop-list:focus {
  background-color: var(--accent-color-light-theme);
  color: var(--header-bg-color-light);
}

.progress {
  height: 64px;
  width: 64px;
  cursor: pointer;
  background-color: rgba(79, 46, 232, .3);
  border-radius: 50%;
  align-items: center;
  justify-items: center;
  display: grid;
  position: fixed;
  bottom: 20px;
  right: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, .2);
}

.progress-value {
  height: 54px;
  width: 54px;
  background-color: #4f2ee8;
  border-radius: 50%;
  align-items: center;
  justify-items: center;
